@font-face {
  font-family: 'Neuzeit Office';
  src: url(/static/media/NeuzeitOffice-Regular.bd59597b.eot);
  src: url(/static/media/NeuzeitOffice-Regular.bd59597b.eot?#iefix)
      format('embedded-opentype'),
    url(/static/media/NeuzeitOffice-Regular.fe5c4fae.woff2) format('woff2'),
    url(/static/media/NeuzeitOffice-Regular.68a9edca.woff) format('woff'),
    url(/static/media/NeuzeitOffice-Regular.53dba4cd.ttf) format('truetype'),
    url(/static/media/NeuzeitOffice-Regular.94e07f1c.svg#NeuzeitOffice-Regular) format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Neuzeit Office';
  src: url(/static/media/NeuzeitOffice-Bold.94fe9863.eot);
  src: url(/static/media/NeuzeitOffice-Bold.94fe9863.eot?#iefix) format('embedded-opentype'),
    url(/static/media/NeuzeitOffice-Bold.b44a3aaf.woff2) format('woff2'),
    url(/static/media/NeuzeitOffice-Bold.8cf3d923.woff) format('woff'),
    url(/static/media/NeuzeitOffice-Bold.b5ca6f0b.ttf) format('truetype'),
