@font-face {
  font-family: 'Neuzeit Office';
  src: url(/static/media/NeuzeitOffice-Italic.87de9e75.eot);
  src: url(/static/media/NeuzeitOffice-Italic.87de9e75.eot?#iefix)
      format('embedded-opentype'),
    url(/static/media/NeuzeitOffice-Italic.318a88ea.woff2) format('woff2'),
    url(/static/media/NeuzeitOffice-Italic.91b416d4.woff) format('woff'),
    url(/static/media/NeuzeitOffice-Italic.ad24728e.ttf) format('truetype'),
    url(/static/media/NeuzeitOffice-Italic.307bd5d0.svg#NeuzeitOffice-Italic) format('svg');
