  font-style: italic;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Neuzeit Office', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.table-bordered,
.table-bordered td,
.table-bordered th {
  border: 1px solid #4a4a4a;
}

html {
  font-size: 14px;
}

button,
input,
optgroup,
select,
textarea {
  font-family: 'Neuzeit Office';
}

h1,
h2,
h3,
h4 {
  font-family: 'Neuzeit Office';
  font-weight: normal;
  line-height: 1.25;
  letter-spacing: 0.5px;
}

h1 {
  font-size: 28px;
  font-weight: normal;
}

h2 {
  font-size: 21px;
  font-weight: normal;
  letter-spacing: 0;
}

h3 {
  font-size: 17px;
  letter-spacing: 0;
}

h4 {
  font-size: 12px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0;
  margin: 4px 0px;
}

h5 {
  font-size: 15px;
  letter-spacing: 0;
  margin: 3px 0;
  font-weight: 700;
}

.table tbody + tbody {
  border-top: 1px solid black;
}
.table thead th {
  border-bottom: 2px solid black;
}

.Modal {
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  background-color: white;
  width: 50%;
  height: 50%;
  z-index: 999;
  border-radius: 5px;
  outline: none;
  box-shadow: 0 12px 24px 0 rgba(162, 124, 9, 0.25);
  opacity: 0;
  transition: opacity 400ms ease-in-out;
}
.ReactModal__Content--after-open {
  opacity: 1;
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #10101030;
  z-index: 999;
  overflow: scroll;
}

.announcement-modal {
  width: 40%;
  float: right;
  margin-top: 7rem;
  height: 500px;
  overflow: 'scroll';
  outline: none;
}

/* .my-modal-dialog {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
} */
.my-modal {
  height: 100vh;
  width: 100vw;
}

.modal-dialog {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}
.modal-content {
  height: auto;
  width: 100vw;
  min-height: 100%;
  border-radius: 0;
  padding: 20px;
  border: none;
}

